.WrapIntro {
  position: relative;
  /* background-color: blue; */
  width: 110px;
  height: 265px;
}
.WrapTitle {
  margin: 10px;
  height: 50px;
  background-size: contain;
  background-repeat: no-repeat;
  background-image: url("../../imgs/img_title.png");
  top: 52px;
  position: relative;
}
.TextIntro {
  color: black;
  margin: 82px 15px 0px 15px;
  font-size: 14px;
}
.WrapGuide {
  position: absolute;
  text-align: center;
  width: 70px;
  height: 85px;
  padding: 5px;
  border-radius: 12px;
  bottom: 80px;
  margin: 15px;
  border: 2px solid black;
  /* box-shadow: 2px 2px 4px rgb(96, 96, 96); */
}
.WrapGuide .subtitle {
  background-color: #b3b3b3;
  color: #353535;
  top: -10px;
  left: 10px;
  padding: 0 6px;
  font-size: 16px;
}
.TextGuide {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 60px;
  height: 60px;
  background-color: #828282;
  border: 4px solid black;
  border-radius: 12px;
  margin: 6px;
  font-size: 40px;
  line-height: 65px;
  font-weight: 900;
  color: black;
  cursor: pointer;
  animation: bounce1 2s ease infinite;
}

@keyframes bounce1 {
	0%, 20%, 50%, 80%, 100% {transform: translateY(0);}
	40% {transform: translateY(-5px);}
	60% {transform: translateY(-3px);}
}