  height: 157px;
  position: absolute;
  top: 119px;
  right: 10px;
  padding: 105px 30px 20px 30px;
  border-radius: 60px;
  text-align: center;
  font-size: 20px;
  line-height: 35px;
}
.HelpDesign span{
  font-size: 50px;
  position: absolute;
  top: 60px;
  left: 50%;
  animation: bounce2 2s ease infinite;
}
.HelpDesign p{
  animation: bounce2 2s ease infinite;
}
/*  **********  */
.HelpText{
  width: 90%;
  height: 165px;
  position: absolute;
  top: 469px;
  left: 50%;
  transform: translate(-50%);
  padding: 225px 30px 200px 30px;
  border-radius: 120px;
  text-align: center;
  font-size: 30px;
}
.HelpText span{
  font-size: 120px;
  position: absolute;
  top: 100px;
  left: 48%;
  transform: translate(-50%);
  animation: bounce2 2s ease infinite;
}
.HelpText p{
  animation: bounce2 2s ease infinite;
}

@keyframes bounce2 {
	0%, 20%, 50%, 80%, 100% {transform: translateY(0);}
	40% {transform: translateY(-10px);}
	60% {transform: translateY(-5px);}
}