.style-1 {
  font-family: "Hahmlet-Regular", serif;
  font-weight: 900;
}
.style-2 {
  font-family: "국립박물관문화재단클래식B", serif;
  font-weight: 700;
}
.style-3 {
  font-family: "BMEULJIRO", serif;
}
.style-4 {
  font-family: "SBAggroB", sans-serif;
  font-weight: 500;
}
.style-5 {
  font-family: "양진체", sans-serif;
}
.style-6 {
  font-family: "dimibang_new", serif;
}

@media print {
  body * {
    visibility: hidden;
  }

  #printable-modal-content,
  #printable-modal-content * {
    visibility: visible;
  }

  #printable-modal-content {
    position: absolute;
    left: 0;
    top: 0;
  }
}

.typing-effect::before {
  content: "";
  display: block;
  width: 0;
  height: 100%;
  background-color: inherit;
  animation: typing 2s steps(40, end) forwards; /* Adjust duration as needed */
}

@keyframes typing {
  from {
    width: 0;
  }
  to {
    width: 100%;
  }
}
