.SwitchOuter {
  grid-area: Switch;
  margin: 60px 2px 0px 2px;
  position: relative;
  display: flex;
  overflow: hidden;
  height: 80px;
}
.BtnReset {
  border-radius: 10px;
  background: #353535;
  background-color: rgb(53, 53, 53);
  border: 0px solid #000;
  color: rgb(0, 0, 0);
  padding: 15px;
  height: 62px;
  width: 100%;
  position: relative;
  box-shadow: inset 0 6px 4px gray, 0px 2px 3px black;}
.BtnReset:hover {
  background-color: #ffee00;
}
.ResetIcon {
  top: 8px;
  left: 3%;
  width: 20px;
  height: 20px;
  position: absolute;
}
.ResetText {
  top: 12px;
  left: 50%;
  transform: translateX(-50%);
  color: #888888;
  font-size: 1.6em;
  display: block;
  position: absolute;
}
.BtnDarkLight {
  height: 70px;
  border-radius: 15px;
  background: rgb(185, 185, 185);
  color: rgb(20, 20, 20);
  padding: 20px;
  font-family: "Gothic A1", sans-serif;
  font-size: 1em;
  font-weight: 600;
  line-height: 1.1em;
}

.DesignEditor {
  grid-area: DesignEditor;
  width: 370px;
  height: 258px;
  padding-top: 25px;
  box-sizing: border-box;
  display: flex;
  overflow-x: hidden;
  overflow-y: hidden;
  position: relative;
  z-index: 9999;
  border: 2px solid #353535;
  border-radius: 12px;
  margin: 16px 0px 0px 0px;
}
.Editor {
  width: 65px;
  height: 204px;
  display: flex;
  flex-direction: column;
  justify-content: start;
  align-items: center;
  box-sizing: border-box;
  border: 4px solid rgb(0, 0, 0);
  background-color: #828282;
  padding: 0px;
  position: relative;
  z-index: 999;
  border-radius: 12px;
  top: 12px;
}

.EditorSubtitle {
  font-family: "Gothic A1", sans-serif;
  font-size: 16px;
  color: #353535;
  padding: 0 6px;
  position: absolute;
  z-index: 99999;
  background: #b3b3b3;
  top: 130px;
  left: 12px;
}
.FontSubtitle{
  font-family: "Gothic A1", sans-serif;
  font-size: 14px;
  color: #353535;
  position: absolute;
  z-index: 99999;
  top: 150px;
  left: 70px;
  padding: 2px 6px;
  border-radius: 12px;
  background: #999;
  color: black;
}
.SizeSubtitle{
  font-family: "Gothic A1", sans-serif;
  font-size: 14px;
  color: #353535;
  position: absolute;
  z-index: 99999;
  top: 150px;
  left: 230px;
  padding: 2px 6px;
  border-radius: 12px;
  background: #999;
  color: black;
}
.FontBgSubtitle{  font-family: "Gothic A1", sans-serif;
  font-size: 14px;
  position: absolute;
  z-index: 99999;
  top: 150px;
  left: 300px;
  padding: 2px 6px;
  border-radius: 12px;
  background: #999;
  color: black;
}
.Editor .Param {
  font-size: 1em;
  line-height: 1.1em;
  padding: 18px 12px 0px;
  text-align: center;
  color: rgb(190, 190, 190);
}
.Editor .Scope {
  font-size: 0.7em;
  font-weight: 800;
  padding: 0 4px;
  margin: 4px;
  text-align: center;
  background: rgb(20, 20, 20);
  color: rgb(114, 114, 114);
  border-radius: 5px;
}
.EditorInput {
  transform: rotateZ(-90deg);
  margin: 58px 0 26px 0;
  width: 9em;
  appearance: none;
  background: #ffffff00;
  cursor: pointer;
  z-index: 999999999;
}
.EditorLabel {
  display: block;
  width: 94px;
  height: 35px;
  cursor: pointer;
  border-radius: 8px;
  transition: 250ms;
  transform: rotate(-90deg);
  top: -77px;
  left: 0px;
  position: relative;
}


input:checked + .EditorLabel::before {
  box-shadow: 0 0.08em 0.15em -0.1em rgba(0, 0, 0, 0.5) inset,
    0 0.05em 0.08em -0.01em rgba(255, 255, 255, 0.7), 10em 0 0 0 #ffee00 inset;
  animation: flicker 0.3s infinite 0.3s;
}
input:checked + .EditorLabel::after {
  left: 34px;
}

.EditorInput::-moz-range-track {
  background: #000000;
  box-shadow: 1px -1px 1px 0px rgb(170, 170, 170) inset;
  height: 0.6em;
  border-radius: 20px;
  border: 1px solid rgb(80, 80, 80);
}
.EditorInput::-moz-range-thumb {
  -webkit-appearance: none;
  appearance: none;
  margin-top: -10px;
  background: rgb(0, 0, 0);
  background: linear-gradient(
    90deg,
    rgb(126, 126, 126) 0%,
    rgb(121, 121, 121) 10%,
    rgba(0, 0, 0, 1) 90%,
    rgb(162, 162, 162) 100%
  );
  height: 2em;
  width: 2.5em;
  border: 0px solid ;
  border-radius: 0px;
  border-left: 10px solid black;
  border-right: 8px solid black;
}

